.FieldWrapper--RadioGroup {
  display: block;
}
.Field--RadioGroup .Field-el {
  height: auto;
  display: block;
  padding: 0;
  border: none;
  line-height: initial;
}
.Field--RadioGroup .Field-el label {
  cursor: pointer;
}
.Field--RadioGroup .Field-el:not(:first-of-type) {
  margin-top: 8px;
}
.Field--RadioGroup .Radio-label,
.Field--RadioGroup .Radio-mark {
  display: inline-block;
  vertical-align: top;
}
.Field--RadioGroup .Radio-mark {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #c5c5c5;
  text-align: center;
}
.Field--RadioGroup .Radio-mark::before {
  content: '';
  position: relative;
  display: block;
  margin: 0 auto;
  top: 5px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  opacity: 0;
}
.Field--RadioGroup .Radio-label {
  padding-left: 28px;
  line-height: 20px;
}
.Field--RadioGroup input:checked + .Radio-mark {
  background: #c5c5c5;
}
.Field--RadioGroup input:checked + .Radio-mark::before {
  background-color: #fff;
  opacity: 1;
}
